.pulse1Green {
    position: absolute;  
    width: 79px;
    height: 79px;
    margin: auto; 
    top: 0; 
    left: -20px; 
    bottom: 0; 
    right: 0;
    z-index: 1;
    opacity: 0;
    border: 3px solid rgba(41, 216, 6, 0.1);
    -webkit-animation: pulsejg1 3s linear infinite;
    animation: pulsejg1 3s linear infinite;
    border-radius: 999px;
    box-shadow: inset 0px 0px 15px 10px rgba(41, 216, 6, 0.6);
    box-sizing: border-box;
}

.pulse2Green {
    position: absolute;
    width: 79px;
    height: 79px;
    margin: auto; 
    top: 0; 
    left: -20px; 
    bottom: 0; 
    right: 0;
    z-index: 2;
    opacity: 0;
    border: 1px solid rgba(41, 216, 6, 0.582);
  
    -webkit-animation: pulsejg2 3s linear infinite;
    animation: pulsejg2 3s linear infinite;
    border-radius: 999px;
    box-shadow: inset 0px 0px 12px 5px rgba(41, 216, 6, 0.8);
