@-webkit-keyframes pulsejg1 {
    0% {
        -webkit-transform: scale(1.6);
        opacity: 0;
    }

    50% {
        -webkit-transform: scale(1.6);
        opacity: 0;
    }

    60% {
        -webkit-transform: scale(1.9);
        opacity: .2;
    }

    70% {
        -webkit-transform: scale(2.1);
        opacity: .35;
    }

    80% {
        -webkit-transform: scale(2.25);
        opacity: .2;
    }

    100% {
        -webkit-transform: scale(2.4);
        opacity: 0;
    }
}

@keyframes pulsejg1 {
    0% {
        -webkit-transform: scale(1.6);
                transform: scale(1.6);
        opacity: 0;
    }

    50% {
        -webkit-transform: scale(1.6);
                transform: scale(1.6);
        opacity: 0;
    }

    60% {
        -webkit-transform: scale(1.9);
                transform: scale(1.9);
        opacity: .1;
    }

    70% {
        -webkit-transform: scale(2.1);
                transform: scale(2.1);
        opacity: .25;
    }

    80% {
        -webkit-transform: scale(2.25);
                transform: scale(2.25);
        opacity: .1;
    }

    100% {
        -webkit-transform: scale(2.4);
                transform: scale(2.4);
        opacity: 0;
    }
}

@-webkit-keyframes pulsejg2 {
    0% {
        -webkit-transform: scale(1.6);
        opacity: 0;
    }

    40% {
        -webkit-transform: scale(1.8);
        opacity: .05;
    }

    50% {
        -webkit-transform: scale(2);
        opacity: .1;
    }

    60% {
        -webkit-transform: scale(2.1);
        opacity: .3;
    }

    80% {
        -webkit-transform: scale(2.2);
        opacity: .1;
    }

    100% {
        -webkit-transform: scale(2.3);
        opacity: 0;
    }
}

@keyframes pulsejg2 {
    0% {
        -webkit-transform: scale(1.6);
                transform: scale(1.6);
        opacity: 0;
    }

    40% {
        -webkit-transform: scale(1.8);
                transform: scale(1.8);
        opacity: .05;
    }

    50% {
        -webkit-transform: scale(2);
                transform: scale(2);
        opacity: .1;
    }

    60% {
        -webkit-transform: scale(2.1);
                transform: scale(2.1);
        opacity: .3;
    }

    80% {
        -webkit-transform: scale(2.2);
                transform: scale(2.2);
        opacity: .1;
    }

    100% {
        -webkit-transform: scale(2.3);
                transform: scale(2.3);
        opacity: 0;
    }
}
.customChip {
    cursor: inherit;
    display: flex;
    align-items: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    white-space: nowrap;
    height: 26px;
    border-radius: 10px;
    line-height: 26px;
    text-align: center;
    color: #efefef;
    text-shadow : rgb(0, 0, 0) -1px 0px 1px, rgb(0, 0, 0) 1px 0px 1px, rgb(0, 0, 0) 0px 1px 1px, rgb(0, 0, 0) 0px -1px 1px;
    font-weight: 700;
    font-size : 12px;
    padding-left: 10px;
    padding-right: 10px;
}

.killsDeathsChip {
    background-color: rgba(255, 255, 255, 0.582);
    min-width: 150px;
}

.customChip span {
    display: inline-block;
    height: 26px;
}

.chipLeft {
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px; 
}

.chipRight {
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px; 
}

.detailedStatsContainer {
    min-height: 20px;
    padding: 19px;
    margin-bottom: 20px;
    background-color: #f5f5f5;
    border: 1px solid #e3e3e3;
    border-radius: 4px;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.05);
}

.detailedStatsTable {
    border-spacing: 0px;
}

.detailedStatsTable tr td {
    padding: 3px;
}

.detailedStatsTable tbody tr:last-child td {
    border-top : 1px solid black;
}

.serverName {
    font-size: 20px;
    line-height: 30px;
    text-shadow: rgb(0, 0, 0) -1px 0px 1px, rgb(0, 0, 0) 1px 0px 1px, rgb(0, 0, 0) 0px 1px 1px, rgb(0, 0, 0) 0px -1px 1px;
}
.statsContainer {
    position : fixed;
    top : 0px;
    right : -535px;
    text-align : left;
    color : #efefef;
    font-size : 12pt;
    z-index : 100;  
    height : -webkit-fit-content;  
    height : -moz-fit-content;  
    height : fit-content;
    display: flex;
    width: 600px; /* Safari */
    transition: right 2s;
}

.statsContainerExpanded {
    right : 0px; /* Safari */
    transition: right 2s;
}

.statsTable {
    background : rgba(0,0,0,0.3);
    padding : 12px;
    border: 1px solid rgba(255,255,255,0.2);
    width : 535px;
    display : block;
}

.statsTable th, .statsTable td {
    font-size : 14px;
    line-height : 20px;
    vertical-align : middle;
    text-shadow : rgb(0, 0, 0) -1px 0px 1px, rgb(0, 0, 0) 1px 0px 1px, rgb(0, 0, 0) 0px 1px 1px, rgb(0, 0, 0) 0px -1px 1px;
}

.statsExpander {
    width: 65px;
    background : rgba(0,0,0,0.3);
    border: 1px solid rgba(255,255,255,0.2);
    display: flex;
}

.statsExpander button {
    vertical-align : middle;
    color: white;
}

.inputHidden {
    width : 1px;
    height : 1px;
    position : absolute;
    left : -100%;
    top : -100%
}

.objectiveTooltip {
    background-color : rgba(0,0,0,0.8)!important;
    color : #efefef!important;
    font-size : 8pt!important;
    text-shadow : rgb(0, 0, 0) -1px 0px 1px, rgb(0, 0, 0) 1px 0px 1px, rgb(0, 0, 0) 0px 1px 1px, rgb(0, 0, 0) 0px -1px 1px!important;
}
